import React, { useState, useEffect } from 'react'
import { NavLink,useHistory, useParams } from 'react-router-dom';
import { makeStyles, Grid, TextField, Button, RadioGroup, FormControlLabel, Radio, FormLabel, FormControl } from '@material-ui/core';
import "react-datepicker/dist/react-datepicker.css";


const useStyles = makeStyles((theme) => ({
    h1: {
        marginBottom: '25px',
    },
    textlink: {
        textAlign: 'right',
        marginTop: '20px'
    },
    button: {
        marginTop: '15px',
        marginBottom: '15px'
    },
}));

const Homelogin = () => {
    const history = useHistory();
    const classes = useStyles();
    const [user, setUser] = useState({
        Date: "", name: "", Errors: "", ScreenshotLink: "", Status: "", Category: "", ThemeName: ""
    });
    const { id } = useParams();

    useEffect(() => {
        loadUserDetails();
    }, []);

    const loadUserDetails = async() => {
        const response = await fetch(`/${id}`, {
            method: "GET"
        });

        const setdata = await response.json();
        setUser(setdata);
    }

    let name, value;

    const handleInputs = (e) => {
        console.log(e);
        name = e.target.name;
        value = e.target.value;

        setUser({ ...user, [name]: value });
    }

    const PostData = async (e) => {
        e.preventDefault();

        const { Date, name, Errors, ScreenshotLink, Status, Category, ThemeName } = user;

        const res = await fetch("/addData", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({ Date, name, Errors, ScreenshotLink, Status, Category, ThemeName })
        });

        const data = await res.json();

        if (res.status === 422 || !data) {
            window.alert("Invaild Data");
            console.log("Invaild Data");
        } else {
            window.alert("Data Add Successfully");
            console.log("Data Add Successfully");

            history.push("/all");
        }
    }


    return (
        <div>
            <section className="register">
                <div className="container mt-5">
                    <div className="register-content">
                        <div className="register-form">
                            <Grid container spacing={1}>
                                <Grid item xs>
                                    <h1 className={classes.h1}>Add Data</h1>
                                </Grid>
                                <Grid className={classes.textlink} item xs>
                                    <NavLink to="/all" className="signup-image-link">Show All Data</NavLink>
                                </Grid>
                            </Grid>
                            <form method="POST" className="register-form" id="register-form">
                                <div className="form-group">
                                    <select
                                        name="Category"
                                        label="Enter Category" style={{ width: 1100 }}
                                        value={user.Category} onChange={handleInputs}>
                                        <option value="">Enter Category</option>
                                        <option value="Wordpress Themes">Wordpress Themes</option>
                                        <option value="Shopify Themes">Shopify Themes</option>
                                    </select>
                                    {/* <label htmlFor="password"><i class="zmdi zmdi-lock material-icons-name"></i></label> */}
                                    {/* <TextField label="Enter Category" style={{ width: 1100 }} type="text" variant="outlined" name="Category" id="Category" autoComplete="off" value={user.Category} onChange={handleInputs} /> */}

                                    {/* <input type="text" name="Category" id="Category" autoComplete="off" value={user.Category} onChange={handleInputs} placeholder="Enter Category" /> */}
                                </div>
                                <div className="form-group">
                                    <select
                                        name="ThemeName"
                                        label="Enter ProductName" style={{ width: 1100 }}
                                        value={user.ThemeName} onChange={handleInputs}>
                                        <option value="">Enter Product Name</option>
                                        <option value="lzrestaurant">lzrestaurant</option>
                                        <option value="Perfect Ecommerce Store">Perfect Ecommerce Store</option>
                                        <option value="Gurukul Education">Gurukul Education</option>
                                        <option value="Play School Kindergarten">Play School Kindergarten</option>
                                        <option value="Akhada Fitness Gym">Akhada Fitness Gym</option>
                                        <option value="LZ Real Blog">LZ Real Blog</option>
                                        <option value="Suraksha Security Guard">Suraksha Security Guard</option>
                                        <option value="Vivah Royal Wedding">Vivah Royal Wedding</option>
                                        <option value="LZ Interior Decor">LZ Interior Decor</option>
                                        <option value="Urja Solar Energy">Urja Solar Energy</option>
                                        <option value="LZ Charity Welfare">LZ Charity Welfare</option>
                                        <option value="LZ Fashion Ecommerce">LZ Fashion Ecommerce</option>
                                        <option value="Chalak Driving School">Chalak Driving School</option>
                                        <option value="Nataraj Dance Studio">Nataraj Dance Studio</option>
                                        <option value="LZ Food Recipee">LZ Food Recipee</option>
                                        <option value="Multicolor Business">Multicolor Business</option>
                                        <option value="LZ Toy Store">LZ Toy Store</option>
                                        <option value="Nikhar Spa Salon">Nikhar Spa Salon</option>
                                        <option value="Niwas Resort Hotel">Niwas Resort Hotel</option>
                                        <option value="LZ One Page">LZ One Page</option>
                                        <option value="Meditation And Yoga">Meditation And Yoga</option>
                                        <option value="Nirman Construction">Nirman Construction</option>
                                        <option value="Negocio Business">Negocio Business</option>
                                        <option value="Doctor Clinic">Doctor Clinic</option>
                                        <option value="Panadero Bakery">Panadero Bakery</option>
                                        <option value="Expert Lawyer">Expert Lawyer</option>
                                        <option value="Supermarket Ecommerce">Supermarket Ecommerce</option>
                                        <option value="LZ Computer Repair">LZ Computer Repair</option>
                                        <option value="Expert Movers">Expert Movers</option>
                                        <option value="Tattoo Expert">Tattoo Expert</option>
                                        <option value="LZ Cleaning Services">LZ Cleaning Services</option>
                                        <option value="Utsav Event Planner">Utsav Event Planner</option>
                                        <option value="Inside Tours">Inside Tours</option>
                                        <option value="Washing Center">Washing Center</option>
                                        <option value="Coming Soon">Coming Soon</option>
                                        <option value="Digital Marketing">Digital Marketing</option>
                                        <option value="Hexagon">Hexagon</option>
                                        <option value="Vancura">Vancura</option>
                                        <option value="Multi Mobile App">Multi Mobile App</option>
                                        <option value="Expert Carpenter">Expert Carpenter</option>
                                        <option value="The Pet Clinic">The Pet Clinic</option>
                                        <option value="LZ Software Company">LZ Software Company</option>
                                        <option value="Expert Mechanic">Expert Mechanic</option>
                                        <option value="Laundry Master">Laundry Master</option>
                                        <option value="Multi Sports">Multi Sports</option>
                                        <option value="Ultra Print">Ultra Print</option>
                                        <option value="My Resume">My Resume</option>
                                        <option value="Modern Architecture">Modern Architecture</option>
                                        <option value="Water Sports Club">Water Sports Club</option>
                                        <option value="Business Accounting">Business Accounting</option>
                                        <option value="Private Tutor">Private Tutor</option>
                                        <option value="Videography">Videography</option>
                                        <option value="Expert Tailor">Expert Tailor</option>
                                        <option value="Garden Care">Garden Care</option>
                                        <option value="Mobile Repair">Mobile Repair</option>
                                    </select>
                                    {/* <label htmlFor="password"><i class="zmdi zmdi-lock material-icons-name"></i></label> */}
                                    {/* <TextField label="Enter ProductName" style={{ width: 1100 }} type="text" variant="outlined" name="ThemeName" id="ThemeName" autoComplete="off" value={user.ThemeName} onChange={handleInputs} /> */}

                                    {/* <input type="text" name="ThemeName" id="ThemeName" autoComplete="off" value={user.ThemeName} onChange={handleInputs} placeholder="Enter ThemeName" /> */}
                                </div>
                                <div className="form-group">
                                    {/* <label htmlFor="name"><i class="zmdi zmdi-email material-icons-name"></i></label> */}
                                    <TextField id="outlined-basic" style={{ width: 1100 }} label="Enter TesterName" variant="outlined" type="text" name="name" id="name" autoComplete="off" value={user.name} onChange={handleInputs} />

                                    {/* <input type="text" name="name" id="name" autoComplete="off" value={user.name} onChange={handleInputs} placeholder="Enter name" /> */}
                                </div>
                                <div className="form-group">
                                    <TextField
                                        id="date"
                                        label="Enter Date"
                                        style={{ width: 1100 }}
                                        type="date"
                                        value={user.Date} onChange={handleInputs}
                                        defaultValue=""
                                        name="Date"
                                        variant="outlined"
                                        className={classes.textField}
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                    />
                                    {/* <label htmlFor="Date"><i class="zmdi zmdi-account material-icons-name"></i></label> */}
                                    {/* <TextField style={{ width: 1100 }} id="outlined-basic" label="Enter Date" variant="outlined" type="text" name="Date" id="Date" autoComplete="off" value={user.Date} onChange={handleInputs} /> */}

                                    {/* <input type="text" name="Date" id="Date" autoComplete="off" value={user.Date} onChange={handleInputs} placeholder="Enter Date" /> */}
                                </div>
                                <div className="form-group">
                                    {/* <label htmlFor="phone"><i class="zmdi zmdi-phone material-icons-name"></i></label> */}
                                    <TextField
                                        id="outlined-multiline-static"
                                        label="Enter Comments"
                                        multiline
                                        style={{ width: 1100 }} type="text" name="Errors" id="Errors" autoComplete="off" value={user.Errors} onChange={handleInputs}
                                        rows={4}
                                        defaultValue=""
                                        variant="outlined"
                                    />
                                    {/* <input type="text" name="Errors" id="Errors" autoComplete="off" value={user.Errors} onChange={handleInputs} placeholder="Enter Errors" /> */}
                                </div>
                                <div className="form-group">
                                    {/* <label htmlFor="name"><i class="zmdi zmdi-slideshow material-icons-name"></i></label> */}
                                    <TextField label="Enter ScreenshotLink" style={{ width: 1100 }} type="text" variant="outlined" name="ScreenshotLink" id="ScreenshotLink" autoComplete="off" value={user.ScreenshotLink} onChange={handleInputs} />

                                    {/* <input type="text" name="ScreenshotLink" id="ScreenshotLink" autoComplete="off" value={user.ScreenshotLink} onChange={handleInputs} placeholder="Enter ScreenshotLink" /> */}
                                </div>
                                <div className="form-group">
                                    {/* <label htmlFor="password"><i class="zmdi zmdi-lock material-icons-name"></i></label> */}
                                    {/* <TextField label="Enter DeveloperName" style={{ width: 1100 }} type="text" variant="outlined" name="DeveloperName" id="DeveloperName" autoComplete="off" value={user.DeveloperName} onChange={handleInputs} /> */}

                                    {/* <input type="text" name="DeveloperName" id="DeveloperName" autoComplete="off" value={user.DeveloperName} onChange={handleInputs} placeholder="Enter DeveloperName" /> */}
                                </div>
                                <div className="form-group">

                                    <FormControl component="fieldset">
                                        <FormLabel component="legend">Selecte Status</FormLabel>

                                        <RadioGroup aria-label="gender" name="Status" value={user.Status} onChange={handleInputs}>
                                            <FormControlLabel value="Pending" control={<Radio />} label="Pending" />
                                            <FormControlLabel value="Completed" disabled control={<Radio />} label="Completed" />
                                            <FormControlLabel value="Processing" disabled control={<Radio />} label="Processing" />
                                        </RadioGroup>
                                        {/* <label htmlFor="password"><i class="zmdi zmdi-lock material-icons-name"></i></label> */}
                                        {/* <TextField label="Enter Status" type="text" style={{ width: 1100 }} variant="outlined" name="Status" id="Status" autoComplete="off" value={user.Status} onChange={handleInputs} /> */}

                                        {/* <input type="text" name="Status" id="Status" autoComplete="off" value={user.Status} onChange={handleInputs} placeholder="Enter Status" /> */}
                                    </FormControl>
                                </div>
                                <div className="form-group">
                                    {/* <TextField
                                        id="date"
                                        label="Enter SolvedDate"
                                        style={{ width: 1100 }}
                                        type="date"
                                        value={user.SolvedDate} onChange={handleInputs}
                                        defaultValue=""
                                        name="SolvedDate"
                                        variant="outlined"
                                        className={classes.textField}
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                    /> */}
                                    {/* <label htmlFor="password"><i class="zmdi zmdi-lock material-icons-name"></i></label> */}
                                    {/* <TextField label="Enter SolvedDate" style={{ width: 1100 }} type="text" variant="outlined" name="SolvedDate" id="SolvedDate" autoComplete="off" value={user.SolvedDate} onChange={handleInputs} /> */}

                                    {/* <input type="text" name="SolvedDate" id="SolvedDate" autoComplete="off" value={user.SolvedDate} onChange={handleInputs} placeholder="Enter SolvedDate" /> */}
                                </div>
                                <div className="form-group form-button">
                                    <Button
                                        variant="contained"
                                        color="primary"
                                        size="large"
                                        style={{ width: 1100 }}
                                        type="submit" name="signup" id="signup" className="form-submit" value="Add Data" onClick={PostData}
                                        className={classes.button}>
                                        Submit
                                    </Button>
                                    {/* <input type="submit" name="signup" id="signup" className="form-submit" value="Add Data" onClick={PostData} /> */}
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default Homelogin
