import React, { useState } from 'react'
import { NavLink, useHistory } from 'react-router-dom';
import { makeStyles, Grid, Paper, Typography, ButtonBase, TextField, Button, FormControl, FormLabel, RadioGroup, FormControlLabel, Radio } from '@material-ui/core';
import login from "../images/register.png";


const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        margin: 'auto',
        marginTop: '70px',
        maxWidth: 1200,
    },
    textlink: {
        margin: '0px 0px 40px 140px',
    },
    button: {
        margin: '40px 0px 0px 0px',
    },
    image: {
        width: 400,
        height: 400,
        margin: '30px 30px 0px 0px'
    },
    img: {
        maxWidth: '100%',
        maxHeight: '100%',
    },
    signinform: {
        margin: '20px 0px 20px 90px'
    },
    radiobutton: {
        margin: '25px 0px 0px 0px'
    },
}));


export default function Register() {
    const classes = useStyles();

    const history = useHistory();

    const [user, setUser] = useState({
        name: "", email: "", work: "", password: "", cpassword: ""
    });

    let name, value;

    const handleInputs = (e) => {
        console.log(e);
        name = e.target.name;
        value = e.target.value;

        setUser({ ...user, [name]: value });
    }

    const PostData = async (e) => {
        e.preventDefault();

        const { name, email, work, password, cpassword } = user;

        const res = await fetch("/register", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({ name, email, work, password, cpassword })
        });

        const data = await res.json();

        if (res.status === 422 || !data) {
            window.alert("Invaild Registration");
            console.log("Invaild Registration");
        } else {
            window.alert("Registration Successfully");
            console.log("Registration Successfully");

            history.push("/login");
        }
    }


    return (
        <div className={classes.root}>
            <Paper className={classes.paper}>
                <Grid container spacing={1}>
                    <Grid item xs>
                        <Grid item xs container direction="column" spacing={2}>
                            <Grid item xs>
                                <form method="POST" className={classes.signinform}>
                                    <h1 className="form-title">SignUp</h1>
                                    <Grid container spacing={2} alignItems="flex-end">
                                        <Grid item>
                                            <i class="zmdi zmdi-account material-icons-name" />
                                        </Grid>
                                        <Grid item>
                                            <TextField style={{ width: 300 }} type="text" name="name" id="name" autoComplete="off" value={user.name} onChange={handleInputs} label="Enter Your Name" />
                                        </Grid>
                                    </Grid>
                                    <Grid container spacing={2} alignItems="flex-end">
                                        <Grid item>
                                            <i class="zmdi zmdi-email material-icons-name" />
                                        </Grid>
                                        <Grid item>
                                            <TextField style={{ width: 300 }} name="email" id="email" autoComplete="off" value={user.email} onChange={handleInputs} label="Enter Your Email" />
                                        </Grid>
                                    </Grid>
                                    <Grid container spacing={2} alignItems="flex-end">
                                        <Grid item>
                                            <FormControl className={classes.radiobutton} component="fieldset">
                                                <FormLabel component="legend">Select What You Are</FormLabel>
                                                <RadioGroup aria-label="gender" name="work" value={user.work} onChange={handleInputs}>
                                                    <FormControlLabel value="Developer" control={<Radio />} label="Developer" />
                                                    <FormControlLabel value="Tester" control={<Radio />} label="Tester" />
                                                </RadioGroup>
                                            </FormControl>
                                            {/* <TextField style={{ width: 300 }} type="text" name="work" id="work" autoComplete="off" value={user.work} onChange={handleInputs} label="Enter Your Profession" /> */}
                                        </Grid>
                                    </Grid>
                                    <Grid container spacing={2} alignItems="flex-end">
                                        <Grid item>
                                            <i class="zmdi zmdi-lock material-icons-name" />
                                        </Grid>
                                        <Grid item>
                                            <TextField style={{ width: 300 }} type="password" name="password" id="password" autoComplete="off" value={user.password} onChange={handleInputs} label="Enter Your Password" />
                                        </Grid>
                                    </Grid>
                                    <Grid container spacing={2} alignItems="flex-end">
                                        <Grid item>
                                            <i class="zmdi zmdi-lock material-icons-name" />
                                        </Grid>
                                        <Grid item>
                                            <TextField style={{ width: 300 }} type="password" name="cpassword" id="cpassword" autoComplete="off" value={user.cpassword} onChange={handleInputs} label="ReEnter Your Password" />
                                        </Grid>
                                    </Grid>
                                    <Typography className={classes.button} >
                                        <Button
                                            variant="contained"
                                            color="primary"
                                            size="large"
                                            className={classes.button} type="submit" name="signup" id="signup" className="form-submit" value="register" onClick={PostData}>
                                            Register
                                        </Button>
                                    </Typography>
                                </form>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs>
                        <ButtonBase className={classes.image}>
                            <img className={classes.img} alt="complex" src={login} />
                        </ButtonBase>
                        <Typography gutterBottom variant="subtitle1" className={classes.textlink} >
                            <NavLink to="/login" className="signup-image-link">I Am Already Register</NavLink>
                        </Typography>
                    </Grid>
                </Grid>
            </Paper>
        </div>
    )
}

