import React, { useState, useEffect } from 'react'
import { NavLink, useHistory } from 'react-router-dom';
import login from "../images/login.png";
import { makeStyles, Grid, Paper, Typography, ButtonBase, TextField, Button } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        margin: 'auto',
        marginTop: '80px',
        maxWidth: 1200,
    },
    textlink: {
        margin: '0px 0px 40px 200px',
    },
    button: {
        margin: '60px 0px 0px 0px',
    },
    image: {
        width: 400,
        height: 400,
        margin: '30px 30px 0px 80px'
    },
    img: {
        maxWidth: '100%',
        maxHeight: '100%',
    },
    emailinput: {
        marginTop: '20px',
    },
    passwordinput: {
        marginTop: '10px',
    },
    signinform: {
        margin: '80px 30px 0px 80px'
    },
}));

export default function Login() {
    const classes = useStyles();
    const history = useHistory();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [users, setUsers] = useState([]);

    const getAllUsers = async () => {

        const res = await fetch(`/${email}`, {
            method: "GET"
        });
        const data = await res.json();
        setUsers(data);
    }



    const loginUser = async (e) => {
        e.preventDefault();


        const res = await fetch("/login", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({ email, password })
        });

        const data = res.json();

        if (res.status === 400 || !data) {
            window.alert("Invaild Credentials");
        } else {
            getAllUsers();
            // {users.map((user) => (
                history.push(`/home/home${email}`)
            // ))}
        }
    }


    return (
        <div className={classes.bg}>
        <div className={classes.root}>
            <Paper className={classes.paper}>
                <Grid container spacing={1}>
                    <Grid item xs>
                        <ButtonBase className={classes.image}>
                            <img className={classes.img} alt="complex" src={login} />
                        </ButtonBase>
                        <Typography gutterBottom variant="subtitle1" className={classes.textlink} >
                            <NavLink to="/register" className="signup-image-link">Create An Account</NavLink>
                        </Typography>
                    </Grid>
                    <Grid item xs>
                        <Grid item xs container direction="column" spacing={2}>
                            <Grid item xs>
                                <form method="POST" className={classes.signinform}>
                                    <h1 className="form-title">SignIn</h1>
                                    <Grid container spacing={2} alignItems="flex-end" className={classes.emailinput}>
                                        <Grid item>
                                            <i class="zmdi zmdi-email material-icons-name" />
                                        </Grid>
                                        <Grid item>
                                            <TextField style = {{width:300}} type="text" name="email" id="email" autoComplete="off" value={email} onChange={(e) => setEmail(e.target.value)} id="input-with-icon-grid" label="Enter Your Email" />
                                        </Grid>
                                    </Grid>
                                    <Grid container spacing={2} alignItems="flex-end" className={classes.passwordinput}>
                                        <Grid item>
                                            <i class="zmdi zmdi-lock material-icons-name" />
                                        </Grid>
                                        <Grid item>
                                            <TextField style = {{width:300}} type="password" name="password" id="password" autoComplete="off" value={password} onChange={(e) => setPassword(e.target.value)} id="input-with-icon-grid" label="Enter Your Password" />
                                        </Grid>
                                    </Grid>
                                    <Typography className={classes.button} >
                                        <Button
                                            variant="contained"
                                            color="primary"
                                            size="large"
                                            className={classes.button} type="submit" name="signin" id="signin" className="form-submit" value="Log In" onClick={loginUser}>
                                            Log In
                                        </Button>
                                    </Typography>
                                </form>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Paper>
        </div>
        </div>
    );
}

