import React, { useState, useEffect } from 'react';
import { Table, TableHead, TableCell, TableRow, Button, TableBody, makeStyles } from '@material-ui/core'
import { Link } from 'react-router-dom';
import { NavLink, useHistory } from 'react-router-dom';



const useStyles = makeStyles({
    table: {
        width: '100%',
        margin: '0px'
    },
    textlink: {
        width: '80%',
        margin: '0px 0 0 70px'
    },
    thead: {
        '& > *': {
            fontSize: 16,
            background: '#000000',
            color: '#FFFFFF'
        }
    },
    row: {
        '& > *': {
            fontSize: 16
        }
    }
})

const ShowData = () => {

    const [users, setUsers] = useState([]);
    const classes = useStyles();

    useEffect(() => {
        getAllUsers();
    }, []);


    const deleteUserData = async (id) => {
        await fetch(`/${id}`, {
            method: "DELETE"
        });
        getAllUsers();
    }


    const getAllUsers = async () => {

        const res = await fetch("/getData", {
            method: "GET"
        });
        const data = await res.json();
        setUsers(data);
    }


    return (
        <div className={classes.ShowData}>
            {/* <NavLink to="/home" className={classes.textlink}>Add Data</NavLink> */}
            <Table className={classes.table}>
                <TableHead>
                    <TableRow className={classes.thead}>
                        <TableCell>Name</TableCell>
                        <TableCell>Category</TableCell>
                        <TableCell>ThemeName</TableCell>
                        <TableCell>Status</TableCell>
                        <TableCell>Errors</TableCell>
                        <TableCell>ScreenshotLink</TableCell>
                        <TableCell>Date</TableCell>
                        <TableCell></TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {users.map((user) => (
                        <TableRow className={classes.row} key={user._id}>
                            <TableCell>{user.name}</TableCell>
                            <TableCell>{user.Category}</TableCell>
                            <TableCell>{user.ThemeName}</TableCell>
                            <TableCell>{user.Status}</TableCell>
                            <TableCell>{user.Errors}</TableCell>
                            <TableCell>{user.ScreenshotLink}</TableCell>
                            <TableCell>{user.Date}</TableCell>
                            <TableCell>
                                <Button color="primary" variant="contained" style={{ marginRight: 10 }} component={Link} to={`/edit/${user._id}`}>Edit</Button> {/* change it to user.id to use JSON Server */}
                                <Button color="secondary" variant="contained" onClick={() => deleteUserData(user._id)}>Delete</Button> {/* change it to user.id to use JSON Server */}
                            </TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </div>
    )
}

export default ShowData
